  padding: 0;
  overflow-x: hidden;
}

.page {
  scroll-behavior: smooth;
  font-family: Manrope, sans-serif;
}

.page__body {
  margin: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .5s;
